// Product API
export const productAPI = {
  async getProducts(params = {}) {
    const queryString = new URLSearchParams(params).toString();
    return apiCall(`/products?${queryString}`);
  },

  async getProduct(id) {
    return apiCall(`/products/${id}`);
  },

  async getCategories() {
    return apiCall('/products/categories');
  },

  async getBrands() {
    return apiCall('/products/brands');
  },

  async searchProducts(query) {
    return apiCall(`/products/search?q=${encodeURIComponent(query)}`);
  },

  async getProductReviews(productId) {
    return apiCall(`/products/${productId}/reviews`);
  },

  async addProductReview(productId, review) {
    return apiCall(`/products/${productId}/reviews`, {
      method: 'POST',
      body: JSON.stringify(review),
    });
  },
};

// Cart API
export const cartAPI = {
  async getCart() {
    return apiCall('/cart');
  },

  async addToCart(item) {
    return apiCall('/cart', {
      method: 'POST',
      body: JSON.stringify(item),
    });
  },

  async updateQuantity(itemId, quantity) {
    return apiCall(`/cart/${itemId}`, {
      method: 'PUT',
      body: JSON.stringify({ quantity }),
    });
  },

  async removeFromCart(itemId) {
    return apiCall(`/cart/${itemId}`, {
      method: 'DELETE',
    });
  },

  async clearCart() {
    return apiCall('/cart', {
      method: 'DELETE',
    });
  },

  async applyCoupon(couponCode) {
    return apiCall('/cart/coupon', {
      method: 'POST',
      body: JSON.stringify({ couponCode }),
    });
  },
};

